html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

html, body, .root {
  height: 100%;
}
body {
  line-height: 1.5;
}
body, .app {
  background: white;
}

/* books grid */

.books-grid {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.books-grid li {
  padding: 10px 15px;
  text-align: left;
}
/* main page */

.list-books-title {
    padding: 10px 0;
    background: #2e7c31;
    text-align: center;
}

.list-books-title h1 {
    font-weight: 400;
    margin: 0;
    color: white;
}

.list-books-content {
    padding: 0 0 80px;
    flex: 1 1;
}

.open-search {
    position: fixed;
    right: 25px;
    bottom: 25px;
}

.open-search a {
    display: block;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: #2e7d32;
    background-image: url(/my-reads-react/static/media/add.bb9363b8.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 28px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    font-size: 0;
}
.book {
    width: 140px;
}

.book-top {
    position: relative;
    height: 200px;
    display: flex;
}

.info {
    top: 0;
    right: 6%;
}

.overlay {
    top: 24px;
    height: 135px;
    width: 70px;
}

.uk-card p {
    margin: 0;
    padding-left: 5px;
}

.uk-card-footer {
    bottom: 15%;
}

.book-shelf-changer {
    position: absolute;
    right: 0;
    bottom: -30px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #60ac5d;
    background-image: url(/my-reads-react/static/media/arrow-drop-down.78c6f53b.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.book-shelf-changer select {
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
}

/* book cover */

.book-cover {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    background: #eee;
}

/* stars */

.normal {
    color: gold;
}

.empty {
    color: white;
}
.bookshelf-title {
    border-bottom: 1px solid #dedede;
}
.to-top {
    position: fixed;
    bottom: 1%;
    right: 1%;
}
.search-books-bar {
    font-size: 2em;
}

@media screen and (max-width: 500px) {
    .search-books-bar {
        font-size: 1em;
    }
}
